.dropzone .dz-preview .dz-progress {
  z-index: 1000;
  pointer-events: none;
  position: absolute;
  height: 8px;
  border: none;
  bottom: 0;
  left: 0;
  width: 100%;
  -webkit-transform: scale(1);
  overflow: hidden;
}

.dropzone .dz-preview .dz-progress .dz-upload {
  background: white;
  background: linear-gradient(to bottom, white, white);
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 0;
  -webkit-transition: width 300ms ease-in-out;
  -moz-transition: width 300ms ease-in-out;
  -ms-transition: width 300ms ease-in-out;
  -o-transition: width 300ms ease-in-out;
  transition: width 300ms ease-in-out;
}

.dropzone .dz-preview.dz-error .dz-error-message {
  display: block;
}

.dropzone .dz-preview.dz-error:hover .dz-error-message {
  opacity: 1;
  pointer-events: auto;
  cursor: pointer;
}

.dropzone .dz-preview .dz-error-message {
  pointer-events: none;
  z-index: 1000;
  position: absolute;
  display: block;
  display: none;
  opacity: 0;
  -webkit-transition: opacity .3s ease;
  -moz-transition: opacity .3s ease;
  -ms-transition: opacity .3s ease;
  -o-transition: opacity .3s ease;
  transition: opacity .3s ease;
  border-radius: 8px;
  font-size: 13px;
  top: 130px;
  left: -10px;
  width: 140px;
  background: #be2626;
  background: linear-gradient(to bottom, #be2626, #a92222);
  padding: .5em 1.2em;
  color: #fff;
}

.dropzone .dz-preview .dz-error-message:after {
  content: '';
  position: absolute;
  top: -6px;
  left: 64px;
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 6px solid #be2626;
}

#dropzone__container {
  margin: 10px auto;
}

.dz-message i {
  font-size: 30px;
  display: block;
  margin-bottom: 10px;
}

.dropzone .dz-preview .dz-remove {
  font-family: "themify";
  position: absolute;
  top: 0;
  z-index: 30;
  right: 0;
  text-decoration: none;
  padding: 7px;
  border-radius: 3px;
  background-color: #2196f3;
  color: #fff;
  font-size: 16px;
  text-align: center;
  display: block;
  cursor: pointer;
  border: none;
  speak: none;
  font-style: normal;
  font-weight: 600;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
  -webkit-transition: all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
  -moz-transition: all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
  -o-transition: all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
  text-shadow: 0px 32px 32px rgba(20, 34, 44, 0.05), 0px 16px 16px rgba(20, 34, 44, 0.05), 0px 8px 8px rgba(20, 34, 44, 0.05), 0px 4px 4px rgba(20, 34, 44, 0.05), 0px 2px 2px rgba(20, 34, 44, 0.05);
}

.dropzone .dz-preview {
  z-index: 1 !important;
}

.dropzone .dz-preview .dz-remove:hover {
  text-decoration: none;
  background-color: #0c7cd5;
}

.dropzone .dz-preview:nth-child(3) .dz-image {
  border: 3px solid #2196f3 !important;
}

.dz-edit {
  position: absolute !important;
  bottom: 0;
  z-index: 30;
  left: 0;
  display: inline-block;
}

.dz-rotate {
  cursor: pointer;
  text-decoration: none;
  padding: 7px;
  border-radius: 3px;
  background-color: #2196f3;
  color: #fff;
  font-size: 16px;
  text-align: center;
  display: block;
  cursor: pointer !important;
  border: none;
  transition: all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
  -webkit-transition: all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
  -moz-transition: all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
  -o-transition: all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
}

.dz-rotate i {
  cursor: pointer !important;
  font-weight: 900;
  text-shadow: 0px 32px 32px rgba(20, 34, 44, 0.05), 0px 16px 16px rgba(20, 34, 44, 0.05), 0px 8px 8px rgba(20, 34, 44, 0.05), 0px 4px 4px rgba(20, 34, 44, 0.2), 0px 2px 2px rgba(20, 34, 44, 0.05);
}

.dz-rotate:hover {
  text-decoration: none;
  background-color: #0c7cd5;
  color: #fff;
}

.dz-overlay {
  position: absolute !important;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #2196f3;
  opacity: 0.9;
  visibility: visible;
  z-index: 100;
  border-radius: 5px;
  pointer-events: all;
  transition: all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
  -webkit-transition: all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
  -moz-transition: all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
  -o-transition: all 0.75s cubic-bezier(0.23, 1, 0.32, 1);
}

.dz-overlay img {
  width: 50%;
  display: block;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  right: 0;
  left: 0;
  margin: 0 auto;
}

.dz-overlay.hidden {
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
}

.marker {
  position: relative;
  margin: 16px;
  border-radius: 5px;
  position: relative;
  display: inline-block;
  vertical-align: top;
  border: 2px dashed rgba(148, 160, 190, 0.2);
  background-color: white;
  width: 120px;
  height: 120px;
  transition: .2s height;
}

.marker:after {
  font-family: 'themify';
  content: "\e656";
  color: rgba(148, 160, 190, 0.2);
  font-size: 20px;
  text-align: center;
  display: block;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  right: 0;
  left: 0;
  margin: 0 auto;
}

#dropzone__json {
  display: none;
}

